@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
* {
  /* font-size: 10px; */
  margin: 0;
  padding: 0;
}

button:focus {
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
}

.clock {
  margin: auto;
  width: 60%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[5];
      grid-template-columns: repeat(5, 1fr);
  -ms-grid-rows: (1fr)[6];
      grid-template-rows: repeat(6, 1fr);
  font-size: 1.4em;
}

.clock h3 {
  top: center;
  -ms-grid-column: 1;
  -ms-grid-column-span: 5;
  grid-column: 1/6;
  font-size: 2.5em;
  font-family: "Merriweather Sans", sans-serif;
  -ms-grid-row-align: center;
  align-self: center;
}

.clock .break {
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-column: 1/3;
}

.clock .session {
  -ms-grid-column: 4;
  -ms-grid-column-span: 2;
  grid-column: 4/6;
}

.clock section {
  -ms-grid-column: 2;
  -ms-grid-column-span: 3;
  grid-column: 2/5;
  -ms-grid-row: 3;
  -ms-grid-row-span: 3;
  grid-row: 3/6;
  background-image: url(/pomodoro-clock/static/media/tomato.c066f59e.png);
  z-index: 1;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.clock .startStop {
  -ms-grid-column: 3;
  -ms-grid-column-span: 1;
  grid-column: 3/4;
  display: flex;
  grid-gap: 2px;
  gap: 2px;
  height: 50%;
  align-self: center;
  justify-content: space-around;
}

.break #break-label {
  font-weight: bold;
  font-size: 1.6em;
}

.break button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.break #break-decrement {
  color: red;
}

.break p {
  font-family: "Merriweather Sans", sans-serif;
  font-size: 1.6em;
  font-weight: 300;
}

.session #session-label {
  font-weight: bold;
  font-size: 1.6em;
}

.session button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.session #session-decrement {
  color: red;
}

.session p {
  font-family: "Merriweather Sans", sans-serif;
  font-size: 1.6em;
  font-weight: 300;
}

.main {
  position: relative;
  border-radius: 50%;
  box-shadow: 0 4px 8px 0 rgba(179, 2, 2, 0.4), 0 6px 20px 0 rgba(230, 59, 59, 0.2), -2px -7px 4px 0 rgba(179, 2, 2, 0.4);
}

.main #timer-label {
  font-weight: bold;
  font-size: 4em;
  font-family: "Merriweather Sans", sans-serif;
}

.main #time-left {
  font-family: "Orbitron", sans-serif;
  font-size: 3em;
}

.startStop button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.startStop button i {
  -webkit-filter: drop-shadow(3px 8px 5px rgba(20, 20, 20, 0.788));
          filter: drop-shadow(3px 8px 5px rgba(20, 20, 20, 0.788));
}

@media (max-width: 700px) {
  .clock {
    font-size: 1em;
  }
}

.progress-ring {
  position: absolute;
  z-index: -1;
}

.progress-ring .progress-ring_circle {
  stroke-linecap: round;
  fill: rgba(255, 255, 0, 0.336);
  stroke-width: 10px;
  stroke: black;
}
